import React, { useState, useEffect } from 'react';
import './BottomTab.css';
import useWindowDimensions from '../useWindowDimentions';
import ContactMe from '../ContactMe/ContactMe';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { 
    motion, 
    useMotionValue, 
    useTransform
} from 'framer-motion';

import { 
    faArrowUp
} from '@fortawesome/free-solid-svg-icons';

const arrowVariants = {
    up: {
        transform: "rotate(180deg)"
    },
    down: {
        transform: "rotate(0deg)"
    }
}
  
                              
const BottomTab = ( {activeTab, onOpen} ) => {
    const { height, width } = useWindowDimensions();
    const [state, setState] = useState({open: false});

    const isPhone = (width <= 480);

    const dragConstraints = (isPhone)?
                            {
                                top: 0,
                                bottom: height - 30
                            }
                            :
                            {
                                top: height*0.5 - 30,
                                bottom: height - 30
                            }


    const dragTransition = { 
        bounceStiffness: 600, 
        bounceDamping: 30,
    }

    let animate = {
        y: [
            dragConstraints.bottom,
            dragConstraints.bottom - 30, 
            dragConstraints.bottom
        ]
    }

    let y = useMotionValue(dragConstraints.bottom)
    const opacity = useTransform(
        y,
        [dragConstraints.top, dragConstraints.bottom],
        [1, 0]
    );

    if(activeTab !== 'BOTTOM' && !state.open){
        y.set(dragConstraints.bottom)
    }

    useEffect(() => {
        function updateOpen() {
          if(y.current <= dragConstraints.top+40){
            setState({open: true});
          } 
        }
        const unsubscribeY = y.onChange(updateOpen)

        return () => {
          unsubscribeY()
        }
      }, [dragConstraints.top, y])

    const onTap = (event, info) => {
        if (y.current === dragConstraints.bottom){
            setState({open: true});
            y.set(dragConstraints.top);
            onOpen();
        }
        else if (y.current === dragConstraints.top){
            y.set(dragConstraints.bottom);
            setState({open: false});
        }
    }

    const onDrag = (event, info) => {
        if(y.current <= dragConstraints.top+40){
            setState({open: true});
            console.log("OPENNNN")
        }
        else{
            setState({open: false});
        }
    }
   
    return (
        <div className="bottom-tab">
            <motion.div
                className="bottom-tab-arrow"
                animate={animate}
                transition={{ ease: "easeOut", duration: 0.7, delay: 2 }}
                style={{y}}
                drag="y"
                dragConstraints={dragConstraints}
                dragElastic={0.2}
                dragTransition={dragTransition}
                onTap={onTap}
                onDrag={onDrag}
            >
                <motion.div
                    variants={arrowVariants}
                    animate={(state.open)?'up':'down'}
                >
                    <FontAwesomeIcon icon={faArrowUp}/>
                </motion.div>
            </motion.div>
            <motion.div
                className="bottom-tab-content"
                style={{opacity, y}}
            >
                <ContactMe />
            </motion.div>
        </div>
    )
}


export default BottomTab;